
.header{
  background-image: linear-gradient(-20deg, #fc6076 0%, #ff9a44 100%);

}
.header:hover{
  background-image: linear-gradient(-20deg, #d1c05e 0%, #faaca8 100%, #faaca8 100%);
  transform: scale(1);
  transition-delay: 4ms;
transition-timing-function: ease;
}
@import url(https://fonts.googleapis.com/css?family=Open+Sans:600,300);
.hero{
  width:100%;
    height:600px;
    height: 100vh;
	background: linear-gradient(45deg, rgba(255,175,189,.7),  rgba(117, 184, 150, 0.7), rgba(241, 130, 164, 0.7), rgba(212, 238, 237, 0.7)) 0 0 / 1000% no-repeat, url(https://download.unsplash.com/photo-1429277096327-11ee3b761c93) 0 0 / cover no-repeat;
	-webkit-animation: gradientAnimation 40s ease;
	animation: gradientAnimation 40s ease infinite;
}
@-webkit-keyframes gradientAnimation {
	0%   { background-position: 0% 30%, 0 0;}
	50%  { background-position: 100% 70%, 0 0;}
	100% { background-position: 0% 30%, 0 0;}
}
@keyframes gradientAnimation {
	0%   { background-position: 0% 30%, 0 0;}
	50%  { background-position: 100% 70%, 0 0;}
	100% { background-position: 0% 30%, 0 0;}
}
h1 {
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	color: #fff;
	font : normal 600 45px/1 'Open Sans', sans-serif;
	text-align: center;
	white-space: nowrap;
}

.c {
    display: flex;
    width: 100%;
    padding: 4% 2%;
    box-sizing: border-box;
    height: 100vh;
  }
  
  .box {
    height: 90%;
    flex: 1;
    overflow: hidden;
    transition: .5s;
    margin: 0 2%;
    box-shadow: 0 20px 30px rgba(0,0,0,.1);
    line-height: 0;
    background-image: linear-gradient(to top, #c4c5c7 0%, #dcdddf 52%, #ebebeb 100%);
  }
  
  .box > img {
    width: 100%;
    height: calc(80% );
    object-fit: cover; 
    transition: .5s;
  }
  
  .box > span {
    font-size: 3.8vh;
    display: block;
    text-align: center;
    height: 10vh;
    line-height: 2.6;
  }
  
.box:hover { flex: 40%; }
  .box:hover > img {
    width: 90%;
    height: 80%;
   transform: scale(1);
   transition-duration: 2s;
   transition-delay: 100ms;
   
  }
  .box:hover{
    background-image: linear-gradient(to right, #c1c161 0%, #c1c161 0%, #d4d4b1 100%);
  }
   
  .threeD {
    
    color:grey(169, 169, 169);
    white-space: nowrap;
   
    top: 5%;
    right: 50%;
    transform: translate(-50%, -50%);
    font-size: 3em;
    font-family: sans-serif;
    letter-spacing: 0.1em;
    transition: 0.3s;
    color:lavenderblush;
  }
  
  .threeD:hover {
    color:khaki;
    transition: 0.3s;
    transform: scale(1.1)translate(-50%, -50%);
   
    
   
    text-shadow: 1px -1px 0 grey, 1px -2px 0 grey, 1px -3px 0 grey,
      1px -4px 0 grey, 1px -5px 0 grey 0 grey(6, 110, 23) 0 grey,
      1px -8px 0 grey, 5px -13px 15px grey, 5px -13px 25px grey;
  }
  .spinner {
    position: relative;
    margin: auto;
    box-sizing: border-box;
    background-clip: padding-box;
    width: 200px;
    height: 200px;
    border-radius: 100px;
    border: 4px solid rgba(255, 255, 255, 0.1);
    -webkit-mask: linear-gradient(rgba(0, 0, 0, 0.1), #000000 90%);
    transform-origin: 50% 60%;
    transform: perspective(200px) rotateX(66deg);
    animation: spinner-wiggle 1.2s infinite;
  }
  .spinner:before,
  .spinner:after {
    content: "";
    position: absolute;
    margin: -4px;
    box-sizing: inherit;
    width: inherit;
    height: inherit;
    border-radius: inherit;
    opacity: 1;
    border: inherit;
    border-color: transparent;
    animation: spinner-spin 1.2s cubic-bezier(0.6, 0.2, 0, 0.8) infinite, spinner-fade 1.2s linear infinite;
  }
  
  .spinner:before {
    border-top-color: #c2220d;
  }
  
  .spinner:after {
    border-top-color: #30067e;
    animation-delay: 0.3s;
  }
  
  @keyframes spinner-spin {
    100% {
      transform: rotate(360deg);
    }
  }
  @keyframes spinner-fade {
    50% {
      opacity: 3;
    }
    70% {
      opacity: 3;
    }
    90% {
      opacity: 7;
    }
  }
  

  .loader {
    width: 20em;
    height: 20em;
    font-size: 10px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 90%;
    left: 40%;
}

.loader .face {
    position: absolute;
    border-radius: 50%;
    border-style: solid;
    animation: animate 3s linear infinite;
}

.loader .face:nth-child(1) {
    width: 100%;
    height: 100%;
    color: rgb(238, 87, 162);
    border-color: currentColor transparent transparent currentColor;
    border-width: 0.2em 0.2em 0em 0em;
    --deg: -45deg;
    animation-direction: normal;
}

.loader .face:nth-child(2) {
    width: 70%;
    height: 70%;
    color: rgb(31, 99, 245);
    border-color: currentColor currentColor transparent transparent;
    border-width: 0.2em 0em 0em 0.2em;
    --deg: -135deg;
    animation-direction: reverse;
}

.loader .face .circle {
    position: absolute;
    width: 50%;
    height: 0.1em;
    top: 67%;
    left: 20%;
    background-color: transparent;
    transform: rotate(var(--deg));
    transform-origin: left;
}

.loader .face .circle::before {
    position: absolute;
    top: -0.5em;
    right: -0.5em;
    content: '';
    width: 1em;
    height: 1em;
    background-color: currentColor;
    border-radius: 50%;
    box-shadow: 0 0 2em,
                0 0 4em,
                0 0 6em,
                0 0 8em,
                0 0 10em,
                0 0 0 0.5em rgba(255, 255, 0, 0.1);
}

@keyframes animate {
    to {
        transform: rotate(1turn);
    }
}
